import { reactive, ref } from "vue";
import { ReactiveVariable } from "vue/macros";
import {
  FormCheckboxGroupProps,
  FormFieldProps,
} from "@/views/components/form/types";
import { useFormValidate } from "@/use/useFormValidate";
import { requiredRules } from "@/kernel/validation/vuelidate/requiredRules";
import { maxLengthRules } from "@/kernel/validation/vuelidate/maxLengthRules";
import { useRoute, useRouter } from "vue-router";
import { useStore } from "@/store";
import { storeToRefs } from "pinia";
import { PointAddData } from "@/kernel/api/dto/Point";
import { PointEntity } from "@/kernel/entities/Point";
import {
  postPointAdd,
  getPointGet,
  postPointEdit,
} from "@/kernel/api/methods/Point";
import { useStorePartner } from "@/store/partner";

export const usePointForm = () => {
  const router = useRouter();
  const route = useRoute();
  const { selectPartnerId } = storeToRefs(useStorePartner());
  const { pointSubjects } = storeToRefs(useStore());

  const { setFieldRef, validateForm, resetValidations } = useFormValidate();

  type Fields = {
    name: FormFieldProps;
    address: FormFieldProps;
    subject_to_sell: FormCheckboxGroupProps;
    subject_to_buy: FormCheckboxGroupProps;
    region: FormFieldProps;
    district: FormFieldProps;
    locality: FormFieldProps;
    location: FormFieldProps;
    comment: FormFieldProps;
    work_from: FormFieldProps;
    work_to: FormFieldProps;
    operation_duration: FormFieldProps;
  };

  const isInit = ref(false);
  const fields: ReactiveVariable<Fields> = reactive({
    name: {
      value: "",
      id: "name",
      label: "Наименование",
      rules: requiredRules(),
    },
    address: {
      value: "",
      id: "address",
      label: "Адрес",
      rules: requiredRules(),
      val: "",
    },
    subject_to_sell: {
      text: "Продает",
      checked: false,
      value: "subject_to_sell",
      id: "subject_to_sell",
      children: [],
    },
    subject_to_buy: {
      text: "Закупает",
      checked: true,
      value: "subject_to_buy",
      id: "subject_to_buy",
      label: "Тип",
      children: [],
    },
    region: {
      value: "",
      id: "region",
      label: "Регион",
    },
    district: {
      value: "",
      id: "district",
      label: "Район",
    },
    locality: {
      value: "",
      id: "locality",
      label: "Населенный пункт",
    },
    location: {
      value: "",
      id: "location",
      label: "Координаты",
      rules: requiredRules(),
    },
    comment: {
      value: "",
      id: "comment",
      rules: maxLengthRules(),
    },
    work_from: {
      label: "Время начала работы",
      value: "",
      id: "work_from",
      rules: requiredRules(),
    },
    work_to: {
      label: "Время окончания работы",
      value: "",
      id: "work_to",
      rules: requiredRules(),
    },
    operation_duration: {
      label: "Длительность операций",
      value: "",
      id: "operation_duration",
      rules: requiredRules(),
    },
  });

  const createSubject = () => {
    return pointSubjects.value
      ? Object.entries(pointSubjects.value).map(([value, content]) => ({
          value,
          id: value,
          label: content.desc,
          val: content.val,
          checked: false,
        }))
      : [];
  };

  async function init() {
    fields.subject_to_sell.children = createSubject();
    fields.subject_to_buy.children = createSubject();
    isInit.value = true;
  }

  async function initEdit(data: PointEntity) {
    fields.subject_to_sell.children = createSubject();
    fields.subject_to_sell.children.forEach((checkbox) => {
      checkbox.checked = data.subject_to_sell.some(
        (el) => el === checkbox.value
      );
    });
    fields.subject_to_buy.children = createSubject();
    fields.subject_to_buy.children.forEach((checkbox) => {
      checkbox.checked = data.subject_to_buy.some(
        (el) => el === checkbox.value
      );
    });

    fields.name.value = data.name;
    fields.address.value = data.address;
    fields.location.value = data.location;
    fields.work_from.value = data.work_from;
    fields.work_to.value = data.work_to;
    fields.operation_duration.value = String(data.operation_duration);

    data.region && (fields.region.value = data.region);
    data.district && (fields.district.value = data.district);
    data.locality && (fields.locality.value = data.locality);

    fields.comment.value = data.comment || "";

    isInit.value = true;
  }

  function prepareData(): PointAddData {
    const res: Record<string, any> = {};

    Object.entries(fields).forEach(([key, value]) => {
      if (
        value.value &&
        key !== "subject_to_sell" &&
        key !== "subject_to_buy"
      ) {
        res[key] = value.value;
      }
    });
    if (fields.subject_to_buy.children && fields.subject_to_sell.children) {
      const subjectToBuyValue = fields.subject_to_buy.children.reduce(
        (accumulator, current) =>
          accumulator +
          (current.checked && typeof current.val === "number"
            ? current.val
            : 0),
        0
      );
      const subjectToSellValue = fields.subject_to_sell.children.reduce(
        (accumulator, current) =>
          accumulator +
          (current.checked && typeof current.val === "number"
            ? current.val
            : 0),
        0
      );

      if (subjectToBuyValue > 0 || subjectToSellValue > 0) {
        res.subject_to_sell = subjectToSellValue;
        res.subject_to_buy = subjectToBuyValue;
      }
      res.partner_id = route.query.partner_id || selectPartnerId.value;
    }

    return res as PointAddData;
  }

  function clear() {
    Object.values(fields).forEach((el) => (el.value = ""));
    resetValidations();
  }

  function successCallback() {
    clear();
    router.back();
  }

  function fillFromStore(point: PointEntity) {
    fields.name.value = point.name;
    fields.comment.value = point.comment || "";
  }

  async function getPoint(id: number) {
    const res = await getPointGet(id);
    if (!res.error && res.data.info) {
      return new PointEntity(res.data.info);
    }
    return null;
  }
  async function createPoint() {
    const res = await postPointAdd(prepareData());
    if (res.error) {
      throw new Error(
        typeof res.data?.info === "string"
          ? res.data?.info
          : "Ошибка создания пункта"
      );
    }
    return res.data.info.point_id;
  }
  async function editPoint(point: PointEntity) {
    let isError = false;
    const res = await postPointEdit({
      ...prepareData(),
      point_id: point.point_id,
      partner_id: point.partner_id,
    });
    if (res.error) {
      isError = true;
      throw new Error(
        typeof res.data?.info === "string"
          ? res.data?.info
          : "Ошибка редактирования пункта"
      );
    }
    return isError;
  }

  return {
    fields,
    successCallback,
    prepareData,
    setFieldRef,
    validateForm,
    fillFromStore,
    init,
    isInit,
    createPoint,
    getPoint,
    initEdit,
    editPoint,
  };
};
